<template>
  <section class="flex-center flex-column">
    <form>
      <div class="form-group">
        <label>電子信箱</label>
        <input
          type="email"
          class="form-control"
          aria-describedby="emailHelp"
          v-validate="'required|email'"
          name="電子信箱"
          v-model="loginData.user_email"
          placeholder="Email"
        >
        <small class="text-danger">{{errors.first('電子信箱')}}</small>
      </div>
      <div class="form-group">
        <label>密碼</label>
        <input
          type="password"
          class="form-control"
          placeholder="Password"
          name="密碼"
          v-model="loginData.user_password"
          ref="密碼"
        >
      </div>
      <div class="form-group" v-if="isRegister">
        <label>確認密碼</label>
        <input
          type="password"
          class="form-control"
          v-validate="'required|confirmed:密碼'"
          name="確認密碼"
          placeholder="請再輸入一次"
          v-on:keyup.enter="login"
        >
        <small class="text-danger">{{errors.first('確認密碼')}}</small>
      </div>
    </form>
    <section v-if="alertText.text" class="mb-3" :class="{'text-danger':alertText.isDanger?true:false}">
    {{alertText.text}}
    </section>
    <section v-if="!isRegister">
      <button class="btn btn-primary mr-5" @click.prevent="login">登入</button>
      <button class="btn btn-primary" @click.prevent="isRegister = true">加入會員</button>
    </section>
    <section v-else>
      <button class="btn btn-primary mr-5" @click.prevent="register">註冊</button>
      <button class="btn btn-primary" @click.prevent="isRegister = false">返回登入頁面</button>
    </section>
  </section>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
import zh_tw from "vee-validate/dist/locale/zh_TW";
export default {
  data() {
    return {
      loginData: {
        user_email: "",
        user_password: ""
      },
      isRegister: false,
      alertText: {
        text:'',
        isDanger:true
      }
    };
  },
  methods: {
    login() {
      if (this.isValidatorPass) {
        const self = this;
        const login = new AxiosClass("login", "post", this.loginData, true);
        login.doAxios(
          self,
          function(res) {
            self.goback.call(self);
          },
          function(res) {
            self.alertText.text = res.data.text
            self.alertText.isDanger=true
          }
        );
      }
    },
    register() {
      if (this.isValidatorPass) {
        const self = this;
        const register = new AxiosClass("register", "post", this.loginData);
        register.doAxios(
          self,
          function(res) {
            self.alertText.text = res.data.text
            self.alertText.isDanger=false
          },
          function(res) {
            self.alertText.text = res.data.text
            self.alertText.isDanger=true
          }
        );
      }
    },
    goback() {
      window.history.length > 1 ? this.$router.go(-1) : this.$router.push("/");
    }
  },
  computed: {
    isValidatorPass() {
      if (
        this.errors.items.length === 0 &&
        this.loginData.user_email !== "" &&
        this.loginData.user_password !== ""
      ) {
        return true;
      } else {
        return false;
      }
    }
  },
  created() {
    if (this.user_email) {
      console.log(this.user_email);
      this.$router.push("/user");
    }
    this.$validator.localize("zh_tw", {
      messages: zh_tw.messages
    });
  }
};
</script>
<style lang="scss" scoped>
</style>
